@tailwind base;@tailwind components;.wysiwyg{@apply text-base}.wysiwyg h1{@apply text-4xl}.wysiwyg h2{@apply text-3xl}.wysiwyg h3{@apply text-2xl}.wysiwyg h1,.wysiwyg h2,.wysiwyg h3,.wysiwyg h4{margin-bottom:theme("spacing.6")}.wysiwyg p a,.wysiwyg li a{text-decoration:underline;transition:opacity .2s}.wysiwyg p a:hover,.wysiwyg li a:hover{opacity:.75}.wysiwyg ul li{position:relative;padding-left:theme("spacing.3")}.wysiwyg ul::before{content:"•";position:absolute;left:0;color:currentColor}.wysiwyg li+li{margin-top:theme("spacing.3")}.wysiwyg p+p,.wysiwyg p+ul,.wysiwyg ul+p,.wysiwyg ul+ul{margin-top:theme("spacing.4")}.wysiwyg p+h1,.wysiwyg p+h2,.wysiwyg p+h3,.wysiwyg p+h4,.wysiwyg ul+h1,.wysiwyg ul+h2,.wysiwyg ul+h3,.wysiwyg ul+h4{margin-top:theme("spacing.10")}.wysiwyg strong{font-weight:bold}.wysiwyg img{display:block;width:100%;height:auto}@tailwind utilities