/*============================================================================*\
  Default wysiwyg configuration
  Feel free to change all styles
\*============================================================================*/

.wysiwyg {
  @apply text-base;

  h1 {
    @apply text-4xl;
  }

  h2 {
    @apply text-3xl;
  }

  h3 {
    @apply text-2xl;
  }

  h1,
  h2,
  h3,
  h4 {
    margin-bottom: theme('spacing.6');
  }

  p,
  li {
    a {
      text-decoration: underline;
      transition: opacity 0.2s;

      &:hover {
        opacity: 0.75;
      }
    }
  }

  ul {
    li {
      position: relative;
      padding-left: theme('spacing.3');
    }

    &::before {
      content: '•';
      position: absolute;
      left: 0;
      color: currentColor;
    }
  }

  li + li {
    margin-top: theme('spacing.3');
  }

  p,
  ul {
    + p,
    + ul {
      margin-top: theme('spacing.4');
    }

    + h1,
    + h2,
    + h3,
    + h4 {
      margin-top: theme('spacing.10');
    }
  }

  strong {
    font-weight: bold;
  }

  img {
    display: block;
    width: 100%;
    height: auto;
  }
}